import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import {svg1} from './gc1.js'

class GC extends React.Component{
  constructor(props){
    super(props);
    this.state={dot:0,d:[[-10,-10],[-10,-10]],d2:['',''],d3:''}
  }
  handleClick(evt){
    let dot=this.state.dot
    let xy=[evt.clientX+window.pageXOffset-2.5,evt.clientY+window.pageYOffset-2.5]
    let a=<div className='d1' style={{left:xy[0]+'px',top:xy[1]+'px',backgroundColor:dot===0?'red':'blue'}} key={dot}/>
    const d=this.state.d
    d[dot]=xy
    const d2=this.state.d2
    d2[dot]=a
    console.log([d,d2,dot])
    this.setState({d:d,d2:d2,dot:1-dot})
  }
  distance(){
    let d=this.state.d;
    return Math.round(Math.sqrt((d[0][0]-d[1][0])**2+(d[1][1]-d[0][1])**2))
  }
  f(pos){
    let x0=37.7798
    let y0=-80.3464
    let x=pos.coords.latitude
    let y=pos.coords.longitude
    console.log([x,y])
    let cc=180/3958/Math.PI/1760
    let cd=180/3958/Math.PI/1760/Math.cos(x*Math.PI/180)
    console.log([(x0-x)/cd,(y-y0)/cc])
    this.setState({d3:<div className='d1' style={{left:(y-y0)/cc+'px',top:(x0-x)/cd+'px',backgroundColor:'#00ffff'}}/>})
  }
  updCurrLoc(){
    navigator.geolocation.getCurrentPosition(pos=>(this.f(pos)))
  }
  div2(){
    let d=this.state.d
    return {position:'absolute',left:(d[0][0]+d[1][0]-50)/2,top:(d[0][1]+d[1][1]-30)/2,height:'30px',width:'50px',border:'1px solid black',borderRadius:'10px',backgroundColor:'#cccccc',opacity:.8}
  }
  render(){
    return (
      <div>
        <div style={{zIndex:0}} onClick={evt=>this.handleClick(evt)}>
           {svg1()}{this.state.d2[0]}{this.state.d2[1]}{this.state.d3}
        </div>
        <div style={this.div2()} onClick={()=>this.updCurrLoc()}>
          <p>{this.distance()}</p>
        </div>
      </div>
    )
  }
}

ReactDOM.render(<GC />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
